footer{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background: #F6F3EE;
}

.footer-top{
    display: flex;
    align-items: center;
}

.footer-top-links{
    width: 50%;
    height: 100%;
    padding: 4em;
    display: flex;
    justify-content: space-between;
}
.footer-top-links > div{
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 0 2em;
}
.footer-top-links > div p{
    font-size: 1.2rem;
    white-space: nowrap;
}

.footer-top-contact{
    display: flex;
    flex-direction: column;
    gap: 1em;
    width: 50%;
    padding: 4em;
}
.footer-top-contact p{
    color: var(--blue-dark);
    font-weight: 300;
    font-size: 3rem;
    margin: 0;
}
.footer-top-contact-input{
    position: relative;
    background: rgb(213, 213, 213);
    border-radius: 15px;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
}
.footer-top-contact-input input{
    width: 90%;
    padding: 1em;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1.2rem;
}
.footer-top-contact-input span{
    position: absolute;
    right: 1em;
}


.footer-bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2em 4em;
}
.footer-copyright{
    color: var(--blue-dark);
    display: flex;
    align-items: center;
    gap: 0.2em;
}
.footer-copyright p{
    font-size: 0.8rem;
    color: var(--blue-dark);
}

.footer-socials{
    display: flex;
    align-items: center;
    gap: 1em;
}
.footer-socials-p{
    color: var(--blue-dark);
    font-size: 0.9rem;
    margin: 0;
}
.footer-links{
    position: relative;
    color: var(--blue-dark);
    font-size: 0.9rem;
    text-decoration: none;
    height: 1.6em;
    display: flex;
    align-items: flex-end;
    margin: 0;
}
.footer-links:hover::before{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
.footer-links::before{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background: var(--orange);
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    transition: all 0.3s;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    
}

@media only screen and (max-width: 480px){
    .footer-top{
        flex-direction: column-reverse;
    }
    .footer-top-links, .footer-top-contact{
        width: 100%;
        padding: 2em;
    }
    .footer-top-links > div{
        padding: 0;
    }

    .footer-bottom{
        padding: 2em;
        flex-direction: column;
    }
}

