.projects-canvas-fixed, .projects-canvas{
    z-index: -1;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    margin-bottom: 100vh;
    transition: position 0.5s ease;
}
.projects-canvas-fixed{
    z-index: 10;
}
.project-info{
    position: absolute;
    z-index: 99;
    bottom: 4em;
    left: 4em;
    width: max-content;
}
.project-info h1{
    color: white;
    font-size: 4rem;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: var(--blue-dark);
    font-weight: 400;
}