:root{
  --main-clr: #ffffff;
  --font-clr: #bebcbc;
  --font-light: #cfcfcf;
  --blue: #2222ff;
  --blue-dark: #000011;
  --orange: #FFDD00;
  --bg: #F6F3EE;
  --radius: 5px;
}

*{
  box-sizing: border-box;
}

html, body{
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Bebas Neue', cursive;
}
body{
  overflow-x: hidden;
}

body h1{
  font-family: 'Raleway', sans-serif;
  color: white;
  margin: 0;
}

body p, small, span, label{
  font-family: 'Raleway', sans-serif;
  margin: 0;
}


/* Buttons */
.btn{
  position: relative;
  width: max-content;
  align-self: flex-end;
  display: flex;
  align-items: center;
  gap: 0.5em;
  padding: 1.5em 2em;
  border-radius: 35px;
  border: none;
  cursor: pointer;
  color: #000011;
  text-decoration: none;
  background: rgb(208, 208, 208);
  font-family: 'Raleway', sans-serif;
}
.btn:hover{
  background: transparent;
  color: white;
}
.btn::before{
  z-index: -1;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 35px;
  background: var(--blue-dark);
  clip-path: circle(0.1% at 0 50%);
  transition: 0.3s;
  transform-origin: left;
}
.btn:hover::before{
  clip-path: circle(71.7% at 49% 49%);
}
.btn-span{
  transition: transform 0.3s;
  display: flex;
  align-items: center;
  gap: 0.5em;
  font-size: 1rem;
}
.btn:hover .btn-span{
  transform: translateX(5px);
  color: var(--orange);
}

.main-btn{
  position: relative;
  width: max-content;
  display: flex;
  align-items: center;
  gap: 0.5em;
  background: var(--orange);
  color: white;
  padding: 2em;
  border-radius: 35px;
  border: none;
  cursor: pointer;
}
.main-btn::after{
  content: "Get in touch";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  color: white;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(20px);
  opacity: 0;
  transform-origin: bottom;
  transition: all 0.3s;
}
.main-btn:hover::after{
  transform: translateY(0);
  opacity: 1;
}
.main-btn:hover{
  color: white;
}
.main-btn::before{
  z-index: -1;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 35px;
  background: var(--blue-dark);
  clip-path: circle(0.1% at 0 50%);
  transition: 0.3s;
  transform-origin: left;
}
.main-btn:hover::before{
  clip-path: circle(71.7% at 49% 49%);
}
.main-btn span{
  transition: transform 0.3s;
  display: flex;
  align-items: center;
  gap: 0.5em;
  font-size: 1rem;
  transform: translateX(-20px);
  opacity: 0;
}
.main-btn:hover .main-btn-span{
  transform: translateX(5px);
  opacity: 1;
}

.secondaty-titles{
  font-size: 5rem;
  font-weight: 500;
  line-height: 0.8em;
  color: var(--blue-dark);
}